<script>
import { ref } from "vue";
// import { useAuthStore } from "@/stories/mainStore.js";
import RegComponent from "@/components/RegComponent.vue";
// import TransferComponent from "@/components/TransferComponent.vue";
import InsertComponent from "@/components/InsertComponent.vue";
// import MenuComponent from "@/components/MenuComponent.vue";
import SelectComponent from "@/components/SelectComponent.vue";
// import DetailComponent from "@/components/DetailComponent.vue";
import MappingComponent from "@/components/MappingComponent.vue";

import { useMenuStore } from "@/stories/mainStore.js";

export default {
  name: "HomeView",
  components: {
    RegComponent,
    InsertComponent,
    // MenuComponent,
    SelectComponent,
    MappingComponent,
  },
  setup() {
    //
    const menuStore = useMenuStore();
    const tabIdx = ref(0);

    const tabsMap = {
      ship_tag: "Tag",
      host: "Host",
      network: "Network",
      ship_access_group: "Group Ship Map",
      ship_access_user: "Group User Map",
      groupport: "Group Port Map",
      ship_tag_mapping: "Ship Tag",
      // time_zone,
    };

    // const tabs = ref([
    //   "Network",
    //   "Tag",
    //   "Host",
    //   "Ship Tag",
    //   "Group Port Map",
    //   "Group Ship Map",
    //   "Group User Map",
    // ]);

    const tabArr = [];
    menuStore.tabs.get(location.pathname.replace(/\//, "")).map((item) => {
      if (!tabsMap[item.name]) {
        console.warn(`누락 ${item.name}`);
      } else {
        tabArr.push(tabsMap[item.name]);
      }
    });
    const tabs = ref(tabArr);

    return {
      tabs,
      tabIdx,
    };
  },
};
</script>

